<script>
export default {
  data () {
    return {
      isActive: false,
      tabMenuWidth: 0,
      tabWidth: 0,
      currentIdx: 0,
      currentTabIdx: 0,
      tabMoveValue: 0,
      questionList: [
        { isActive: true, question: '1' },
        { isActive: false, question: '2' },
        { isActive: false, question: '3' },
        { isActive: false, question: '4' },
        { isActive: false, question: '5' },
        { isActive: false, question: '6' },
        { isActive: false, question: '7' },
        { isActive: false, question: '8' },
        { isActive: false, question: '9' },
        { isActive: false, question: '10' },
        { isActive: false, question: '11' },
        { isActive: false, question: '12' },
        { isActive: false, question: '13' },
        { isActive: false, question: '14' }
      ]
    }
  },
  computed: {
    disablePrevButton () {
      return this.currentTabIdx === 0 || this.questionList.length <= 10
    },
    disableNextButton () {
      return this.currentTabIdx >= this.tabLimit - 1 || this.questionList.length <= 10
    },
    tabLimit () {
      return Math.ceil(this.questionList.length / 10)
    }
  },
  mounted () {
    this.updateMenuWidth()

    this.$nextTick(() => {
      window.addEventListener('resize', this.updateMenuWidth)
      this.updateMenuWidth()
    })
  },
  methods: {
    updateMenuWidth () {
      this.tabMenuWidth = this.$refs.questionTab.offsetWidth - 4
      this.tabWidth = (this.$refs.questionTab.offsetWidth - 36) / 10 + 'px'
    },
    tabEvent (idx) {
      this.questionList.forEach((el, index) => {
        if (index === idx) {
          el.isActive = true
        } else {
          el.isActive = false
        }
      })
    },
    btnEvent (value) {
      if (value === 'next') {
        this.currentTabIdx = Math.min(this.currentTabIdx + 1, this.tabLimit - 1)
      } else {
        this.currentTabIdx = Math.max(this.currentTabIdx - 1, 0)
      }

      this.tabMoveValue = this.currentTabIdx > 0
        ? (this.$refs.questionTab.offsetWidth * this.currentTabIdx) + (4 * this.currentTabIdx)
        : 0

      this.$refs.questionTab.style.transform = `translateX(-${this.tabMoveValue}px)`
    }
  }
}
</script>
