<script>
export default {
  data () {
    return {
      studentData: [
        { name: '', isActive: false, inClass: true, group: 1 },
        { name: '', isActive: false, inClass: true, group: 2 },
        { name: '', isActive: false, inClass: true, group: 1 },
        { name: '', isActive: false, inClass: true, group: 1 },
        { name: '', isActive: false, inClass: true, group: 1 },
        { name: '', isActive: false, inClass: true, group: 2 },
        { name: '', isActive: false, inClass: true, group: null },
        { name: '', isActive: false, inClass: true, group: 3 },
        { name: '', isActive: false, inClass: false, group: 3 },
        { name: '', isActive: false, inClass: false, group: 3 },
        { name: '', isActive: false, inClass: false, group: 3 },
        { name: '', isActive: false, inClass: false, group: null },
        { name: '', isActive: false, inClass: false, group: null },
        { name: '', isActive: false, inClass: false, group: null },
        { name: '', isActive: false, inClass: false, group: null },
        { name: '', isActive: false, inClass: false, group: null }
      ]
    }
  },
  mounted () {
    // this.popupOpen(11)
  },
  methods: {
    popupOpen (index) {
      const target = document.querySelectorAll('.modal_layer_popup')[index - 1]
      target.classList.add('active')
    },
    closePopup (e) {
      e.target.closest('.modal_layer_popup').classList.remove('active')
    },
    selectStudent (currentIndex) {
      this.studentData.forEach((e, index) => {
        if (index === currentIndex) {
          e.isActive ? e.isActive = false : e.isActive = true
        }
      })
    },
    selectThumb (currentIndex) {
      this.$store.state.bgImagesData.bgImages.forEach((e, index) => {
        e.isActive = index === currentIndex
      })
    }
  }
}
</script>
