<template>
  <!--   class  activity_contents -->
  <div
    class="contents activity_contents"
    :class="{ header_slide: header}"
  >
    <div class="title_box">
      <div class="btn_back_wrap edit">
        <button type="button" title="" class="btn_back"></button>
        <h2>1</h2>
      </div>

      <div class="menu_box">
        <!-- top_list li     class active    -->
        <div class="student_list_wrap" ref="studentListWrap">
          <ul class="top_list" ref="topList">
            <li>
              <!-- <span class="active"></span> -->
              <strong></strong>
              <em></em>
            </li>
            <li>
              <!-- <span class="active"></span> -->
              <strong></strong>
              <em></em>
            </li>
            <li>
              <!-- <span class="active"></span> -->
              <strong></strong>
              <em></em>
            </li>
          </ul>
          <!--   student_list_wrap   top_list      -->
          <div class="btn_wrap">
            <button type="button" class="btn_more active"></button>
            <button type="button" class="btn_close"></button>
          </div>
        </div>

        <div class="btn_wrap mr8">
          <button
            type="button"
            class="common_btn"
            :class="{color2: btn}"
            @click="btnToggle"
            disabled="disabled"
          ><span>{{ btn == true ? "" : "" }}</span>
          </button>
        </div>
        <button
          type="button"
          class="btn_slide_toggle"
          @click="headerToggle"
        ></button>
      </div>
    </div>

    <div class="inner">
      <div style="background-color: #eee;height: 100%;"></div>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      header: false,
      btn: false
    }
  },
  mounted () {
    this.getListWidth()
    window.addEventListener('resize', this.getListWidth)

    // 
    this.$store.state.toolBarActive = true
    //  
    // this.$store.state.toolBarSetup.isActive = true
  },
  methods: {
    headerToggle () {
      this.header = !this.header
    },
    btnToggle () {
      this.btn = !this.btn
    },
    getListWidth () {
      const parent = this.$refs.studentListWrap
      const ulElement = this.$refs.topList
      let sumWidth = 0

      ulElement.querySelectorAll('li').forEach(liElement => {
        sumWidth += liElement.offsetWidth
      })

      parent.offsetWidth > sumWidth ? parent.classList.remove('active') : parent.classList.add('active')
    }
  }
}
</script>
