<template>
  <div
    class="panel_wrap list_panel"
    :class="{in_class: inClass}"
  >
    <div class="panel_header">
      <div class="inner">
        <div class="title">
          <img :src="require(`@/assets/images/panel/${issueData.logo}.png`)" alt="" />
          <h2><span>[{{issueData.number}}]</span> <strong>{{issueData.title}}</strong></h2>
        </div>
        <div
          class="object_wrap"
          :class="{active: issueData.showObjectives}"
        >
          <button type="button" @click="ObjectiveToggle"><span></span></button>
          <div class="object_content">
            <div class="inner" v-html="issueData.objectives"></div>
          </div>
        </div>
      </div>
      <button
        type="button"
        class="btn_close"
        @click="closePopup"
      >
        <span></span>
      </button>
    </div>
    <div class="panel_body">
      <div class="inner">
        <div class="learning_list_wrap">
          <div>
            <h3></h3>
            <ul class="learning_list">
              <li
                v-for="(list, index) in getLearningList"
                :key="`list${index}`"
                :class="{disabled: list.disabled, toggle_list: list.status === 4}"
              >
                <div class="inner">
                  <div class="title">
                    <div>
                      <img :src="require(`@/assets/images/panel/ico_learn_method${list.disabled ? '_dis':''}${list.method}.svg`)" alt="" />
                      <img :src="require(`@/assets/images/panel/ico_learn_type${list.disabled ? '_dis':''}${list.type}.svg`)" alt="" />
                      <img v-if="list.aiEval" :src="require(`@/assets/images/panel/ico_ai.svg`)" alt="" />
                      <p>{{ list.title }}</p>
                    </div>
                    <!-- list.status 1,2,3  -->
                    <img
                      :style="[list.data ? {'display': 'none'} : '']"
                      :src="0 < list.status && list.status <= 3 ? require(`@/assets/images/panel/ico_learn_status${list.status}.svg`) : ''" alt=""
                    />
                    <!-- list.status 4 -->
                    <div class="learning_object_wrap" v-if="list.status === 4">
                      <button
                        type="button"
                        class="btn_learning_object"
                      >
                        <span @click="showPopup($event)"></span>
                      </button>
                    </div>
                    <!-- list.status 5 -->
                    <span class="ai_recommend" v-if="list.status === 5">AI </span>
                  </div>
                  <div class="content" v-if="list.status === 4">
                    <div class="inner_wrap">
                      <dl><dt></dt><dd>0 ~ 100  </dd></dl>
                      <dl><dt></dt><dd>{{byLevelData.length}}</dd></dl>
                      <ul>
                        <li
                          v-for="(data,index) in byLevelData"
                          :key="`data${index}`"
                        >
                          <p>{{ data.name }} <span>{{ data.score }}</span></p>
                        </li>
                      </ul>
                      <div class="noti_txt">
                        <p>     .</p>
                      </div>
                    </div>
                  </div>
                </div>

                <ul class="sub_menu" v-if="list.status !== 4">
                  <li
                    v-for="(subData, index) in list.data"
                    :key="`subData${index}`"
                  >
                    <div class="inner">
                      <div class="title">
                        <div>
                          <img :src="require(`@/assets/images/panel/ico_learn_type${list.disabled ? '_dis':''}${subData.type}.svg`)" alt="" />
                          <img v-if="subData.aiEval" :src="require(`@/assets/images/panel/ico_ai.svg`)" alt="" />
                          <p>{{ list.title }}</p>
                        </div>
                        <div class="learning_object_wrap">
                          <button
                            type="button"
                            class="btn_learning_object"
                          >
                            <span @click="showPopup($event)"></span>
                          </button>
                        </div>
                      </div>
                      <div class="content">
                        <div class="inner_wrap">
                          <dl><dt></dt><dd>0 ~ 100  </dd></dl>
                          <dl><dt></dt><dd>{{byLevelData.length}}</dd></dl>
                          <ul>
                            <li
                              v-for="(data,index) in byLevelData"
                              :key="`data${index}`"
                            >
                              <p>{{index+1}} {{ data.name }} <span>{{ data.score }}</span></p>
                            </li>
                          </ul>
                          <div class="noti_txt">
                            <p>     .</p>
                          </div>
                        </div>
                      </div>
                    </div>
                  </li>
                </ul>
              </li>
            </ul>
          </div>

          <div>
            <h3>Ai </h3>
            <ul class="learning_list">
              <li
                v-for="(list, index) in getAiTutor"
                :key="`list${index}`"
                :class="{disabled: list.disabled}"
              >
                <div class="inner">
                  <div class="title">
                    <div>
                      <img :src="require(`@/assets/images/panel/ico_learn_method${list.disabled ? '_dis':''}${list.method}.svg`)" alt="" />
                      <img :src="require(`@/assets/images/panel/ico_learn_type${list.disabled ? '_dis':''}${list.type}.svg`)" alt="" />
                      <p>{{ list.title }}</p>
                    </div>
                    <img :src="require(`@/assets/images/panel/ico_learn_status${list.status}.svg`)" alt="" />
                  </div>
                </div>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
    <div class="panel_footer">
      <!--     -->
      <ul class="exclusive_menu" v-if="inClass">
        <li
          v-for="(exMenu, index) in issueData.exclusive"
          :key="`exMenu${index}`"
          >
          <a href="#">{{ exMenu }}</a>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      inClass: true,
      issueData: {
        logo: 'logo_daekyo',
        number: 2,
        title: '  .   .',
        objectives: '     <br> ,      .     <br> ,      .     <br> ,      .     <br> ,      .     <br> ,      .     <br> ,      .     <br> ,      .     <br> ,      .     <br> ,      .     <br> ,      .',
        exclusive: [
          '',
          'AI',
          '',
          ''
        ],
        showObjectives: false
      },
      learningData: [
        {
          disabled: true,
          method: 1,
          type: 1,
          aiEval: false,
          title: ' .  .  .',
          status: 1
        },
        {
          disabled: false,
          method: 1,
          type: 1,
          aiEval: false,
          title: ' .',
          status: 2
        },
        {
          disabled: true,
          method: 1,
          type: 2,
          aiEval: false,
          title: ' .',
          status: 1
        },
        {
          disabled: false,
          method: 1,
          type: 2,
          aiEval: false,
          title: ' .',
          status: 2
        },
        {
          disabled: true,
          method: 1,
          type: 3,
          aiEval: false,
          title: ' .',
          status: 1
        },
        {
          disabled: false,
          method: 1,
          type: 3,
          aiEval: false,
          title: ' .',
          status: 3
        },
        {
          disabled: true,
          method: 1,
          type: 4,
          aiEval: false,
          title: '[//]  ',
          status: 0
        },
        {
          disabled: false,
          method: 1,
          type: 4,
          aiEval: true,
          title: '[//]   [//]  ',
          status: 5
        },
        {
          disabled: false,
          method: 1,
          type: 4,
          aiEval: false,
          title: '[//]   [//]  ',
          status: 5
        },
        {
          disabled: false,
          method: 1,
          type: 4,
          aiEval: false,
          title: '[//]  ',
          status: 3,
          data: [
            {
              type: 4,
              aiEval: true,
              title: '[/ ]  '
            },
            {
              type: 5,
              aiEval: false,
              title: '[/ ] HTML '
            }
          ]
        },
        {
          disabled: true,
          method: 2,
          type: 6,
          title: '[//]   ',
          status: 0
        },
        {
          disabled: false,
          method: 2,
          type: 6,
          title: '[//]   ',
          status: 3,
          data: [
            {
              type: 7,
              title: '[/ ]  '
            }
          ]
        },
        {
          disabled: true,
          method: 3,
          type: 1,
          title: ' .1',
          status: 1
        },
        {
          disabled: false,
          method: 3,
          type: 1,
          title: ' .2',
          status: 2
        },
        {
          disabled: false,
          method: 3,
          type: 1,
          title: ' .3',
          status: 2
        },
        {
          disabled: false,
          method: 3,
          type: 1,
          title: ' .4',
          status: 3
        },
        {
          disabled: false,
          method: 3,
          type: 1,
          title: ' .5',
          status: 3
        }
      ],
      byLevelData: [
        { name: '', score: 80 },
        { name: '', score: 99 },
        { name: '', score: 100 },
        { name: '', score: 80 },
        { name: '', score: 100 },
        { name: '', score: 100 },
        { name: '', score: 60 },
        { name: '', score: 100 },
        { name: '', score: 100 },
        { name: '', score: 45 },
        { name: '', score: 100 },
        { name: '', score: 85 },
        { name: '', score: 32 },
        { name: '', score: 95 },
        { name: '', score: 70 },
        { name: '', score: 100 }
      ]
    }
  },
  computed: {
    getLearningList () {
      const result = this.learningData.filter(el => el.method !== 3)
      return result
    },
    getAiTutor () {
      const result = this.learningData.filter(el => el.method === 3)
      return result
    }
  },
  mounted () {
    document.addEventListener('click', (e) => {
      if (e.target.closest('#container')) {
        this.isShowPopup = false
      }
    })
  },
  methods: {
    ObjectiveToggle () {
      this.issueData.showObjectives = !this.issueData.showObjectives
    },
    showPopup (e) {
      e.target.closest('li').classList.toggle('active')
    },
    closePopup () {
      this.$store.commit('closePopup', 0)
    }
  }
}
</script>
