<script>
import VueDatePicker from '@vuepic/vue-datepicker'
import '@vuepic/vue-datepicker/dist/main.css'
export default {
  components: { VueDatePicker },
  data () {
    return {
      dateA: null,
      dateB: null,
      dateC: null,
      dateD: null,
      dateE: null,
      fileInfo: null,
      picked1: new Date(),
      picked2: new Date(),
      activityData: {
        group: false,
        useRoll: true,
        studentData: [
          { name: '', isActive: false, inClass: true, group: 1, role: null },
          { name: '', isActive: false, inClass: true, group: 2, role: null },
          { name: '', isActive: false, inClass: true, group: 1, role: null },
          { name: '', isActive: false, inClass: true, group: 1, role: '' },
          { name: '', isActive: false, inClass: true, group: 1, role: '' },
          { name: '', isActive: false, inClass: true, group: 2, role: '' },
          { name: '', isActive: false, inClass: true, group: null, role: null },
          { name: '', isActive: false, inClass: true, group: 3, role: null },
          { name: '', isActive: false, inClass: false, group: 3, role: '' },
          { name: '', isActive: false, inClass: false, group: 3, role: '' },
          { name: '', isActive: false, inClass: false, group: 3, role: '' },
          { name: '', isActive: false, inClass: false, group: null, role: '' },
          { name: '', isActive: false, inClass: false, group: null, role: '' },
          { name: '', isActive: false, inClass: false, group: null, role: '' },
          { name: '', isActive: false, inClass: false, group: null, role: '' },
          { name: '', isActive: false, inClass: false, group: null, role: '' }
        ]
      }
    }
  },
  computed: {
    hoursArray () {
      const arr = []
      for (let i = 0; i < 24; i++) {
        arr.push({ text: i < 10 ? `0${i}` : i, value: i })
      }
      return arr
    },
    formattedDateA () {
      const date = this.picked1
      const day = date.getDate()
      const month = date.getMonth() + 1
      const year = date.getFullYear()

      return `${year}.${month}.${day}`
    },
    formattedDateB () {
      const date = this.picked1
      const day = date.getDate()
      const month = date.getMonth() + 1
      const year = date.getFullYear()

      return `${year}.${month}.${day}`
    }
  },
  methods: {
    popupOpen () {
      const target = document.querySelector('.modal_layer_popup')
      target.classList.add('active')
    },
    closePopup (e) {
      e.target.closest('.modal_layer_popup').classList.remove('active')
    },
    getFileChange (e) {
      const file = e.target.files[0]
      if (file) {
        this.fileInfo = {
          name: file.name,
          size: (file.size / 1024).toFixed(2)
        }
      }
    },
    datepickerOpened () {
      console.log('opened')
    },
    datepickerClosed () {
      console.log('closed')
    },
    selectStudent (currentIndex) {
      this.activityData.studentData.forEach((e, index) => {
        if (index === currentIndex) {
          e.isActive ? e.isActive = false : e.isActive = true
        }
      })
    }
  }
}
</script>
